    top: 25px;
    right: 100px;
    text-align: center;
    z-index: 3;
}

.nav-outer-info_2 .element {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nav-outer-info_2 .element h6 {
    font-size: 11px;
    font-weight: 600;
    line-height: 20px;
    z-index: 1;
