/* Sticky menu gallery */

.container-sticky {
    border-radius: 4px;
    height: 100px;
    margin-bottom: 3%;
}

@media screen and (max-width: 767px) {
    .container-sticky {
        display: none !important;
    }
}

.fixed {
    position: fixed;
    top: -5px;
    z-index: 100;
}

.menu-placeholder .fixed {
    height: 20px;
}

.sticky-menu {
    width: 1300px !important;
}

@media screen and (max-width: 1600px) {
    .sticky-menu {
        width: 1024px !important;
    }
}

@media screen and (max-width: 1200px) {
    .sticky-menu {
        width: 700px !important;
    }
}

@media screen and (max-width: 992px) {
    .sticky-menu {
        width: 600px !important;
    }
}

.sticky-menu ul {
    border: 1px #80808075 solid;
    background: rgba(255, 255, 255, 0.972);
    border-radius: 4px;
    text-transform: uppercase;
}
@media screen and (max-width: 1200px) {
    .sticky-menu .menu-item a {
        font-size: 0.8em;
    }
}

.sticky-menu .button-group {
    margin: auto;
    padding: 2% 0 !important;
}

.sticky-menu .menu-item {
    list-style: none;
}

.sticky-menu .menu-item a {
    font-weight: 500;
    color: #8e8e8e;
    padding-bottom: 10px;
}

@media screen and (max-width: 1600px) {
    .sticky-menu .menu-item a {
        padding-bottom: 5px;
    }
}

@media screen and (max-width: 1200px) {
    .sticky-menu .menu-item a {
        padding-bottom: 1px;
    }
}

.sticky-menu .menu-item a:hover {
    border-bottom: 3px solid #c19b76;
}

.sticky-menu .menu-item.active a {
    border-bottom: 3px solid #c19b76;
    color: #c19b76;
}